.more-info {
  display        : flex;
  justify-content: center;
  align-items    : center;
  width          : 12px;
  height         : 12px;
  margin-left    : 8px;
  font-size      : 8px;
  border         : 1px solid var(--more-info-color);
  border-radius  : 50%;
  cursor         : pointer;
}

.more-info__img {
  fill: currentColor;
}
