.heading {
  display        : flex;
  justify-content: space-between;
  align-items    : center;
  max-height     : 76px;
  padding-left   : var(--main-padding);
  padding-right  : 30px;
  padding-top    : 26px;
  padding-bottom : 26px;
  border-bottom  : var(--main-border);
}

.heading__title {
  margin     : 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size  : 14px;
  line-height: 163%;
  color      : var(--dark);
}

@media (max-width: 500px) {
  .heading {
    padding-right: var(--main-padding);
  }
}
