.guest__item {
  display      : grid;
  row-gap      : 22px;
  padding-top  : 26px;
  padding-left : var(--main-padding);
  padding-right: var(--main-padding);
}

.guest__row {
  display              : grid;
  grid-template-columns: 27px repeat(3, 1fr);
  column-gap           : 17px;
  align-items          : center;

  &:last-child {
    padding-bottom: 38px;
    border-bottom : var(--main-border);
  }
}

.guest__item-col {
  display    : flex;
  align-items: center;
  flex-wrap  : wrap;

  &--second {
    grid-column-start: 2;
  }

  &--full {
    grid-column-end: -1;
  }
}

.guest__item-value {
  margin        : 0;
  font-size     : 12px;
  line-height   : 185%;
  letter-spacing: 0;

  &--head {
    font-size     : 13px;
    letter-spacing: 0.03em;
  }
}

.guest__photo {
  margin-left: 8px;
}

.guest__social {
  margin-left: 20px;

  &--whatsapp {
    fill: var(--whatsapp);
  }
}


.guest__social-link {
  &:not(:last-child) {
    margin-right: 10px;
  }
}

.guest__img-social {
  &--whatsapp {
    fill: var(--whatsapp);
  }

  &--telegramm {
    fill: var(--telegramm);
  }

  &--disable {
    fill: var(--disable);
  }
}

.guest__item-name {
  margin        : 0;
  margin-right  : 6px;
  font-size     : 11px;
  color         : var(--dark);
  letter-spacing: 0;
}

.guest__flyer-divider {
  margin-left : 10px;
  margin-right: 10px;
}

@media (max-width: 767px) {
  .guest__row {
    grid-template-columns: 27px 1fr;
    row-gap              : 10px;
  }

  .guest__item-col {
    grid-column-start: 2;

    &--avatar {
      grid-column-start: 1;
    }
  }
}
