.info {
  display              : grid;
  grid-template-columns: 1fr;
  grid-auto-rows       : min-content;
  row-gap              : var(--main-row-gap);
}

.info__item {
  background-color: var(--white);
  border-radius   : var(--main-border-radius);
  box-shadow      : var(--main-shadow);
}

@media (max-width: 1200px) {
  .info {
    order: 1;
  }
}
