body {
  min-width     : 320px;
  margin        : 0;
  padding       : 0;
  font-size     : 12px;
  line-height   : 130%;
  letter-spacing: 0.03em;
  font-family   : "Roboto", sans-serif;
  color         : var(--main-grey);
  background    : var(--light-grey);
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height   : auto;
}

*,
::before,
::after {
  box-sizing: border-box;
}
