.route__item {
  padding       : var(--main-padding);
  padding-bottom: 42px;
  border-bottom : 1px dashed rgba(212, 219, 231, 0.7);

  &:last-child {
    border-bottom: none;
  }
}

.route__head {
  display              : grid;
  grid-template-columns: 24px 1fr max-content;
  column-gap           : 30px;
  margin-bottom        : var(--main-row-gap);
}

.route__date {
  margin-top: 6px;
}

.route__company {
  margin-top   : 7px;
  margin-bottom: 13px;
}

.route__divider {
  margin-left : 4px;
  margin-right: 2px;

  &::before {
    content: "\0000b7";
  }
}

.route__date-value {
  padding         : 3px 10px;
  opacity         : 0.8;
  border-radius   : 5px;
  background-color: var(--route-date-bg);
}

.route__duration-img {
  fill  : none;
  stroke: var(--main-grey);
}

.route__details {
  display              : grid;
  grid-template-columns: 24px minmax(0, 416px) 1fr;
  column-gap           : 30px;
}

.route__icon-place {
  width        : 9px;
  height       : 9px;
  border       : 1px solid var(--duration-color);
  border-radius: 12px;

  &:first-child {
    margin-top: 15px;
  }

  &:last-child {
    margin-bottom: 17px;
  }
}

.route__icon-duration {
  flex         : 1;
  width        : 1px;
  margin-top   : 2px;
  margin-bottom: 2px;
  border-right : 1px solid var(--duration-color);
}

.route__incons {
  display       : flex;
  flex-direction: column;
  align-items   : center;
}

.route__flight-info {
  display              : grid;
  grid-template-columns: 67px 1fr;
}

.route__flight-time {
  padding-top: 13px;
  font-weight: 500;
  font-family: "Montserrat", sans-serif;
  font-size  : 14px;
  color      : var(--dark);
}

.flight__airport-text {
  margin: 0;
}

.flight__airport-abbr {
  margin-right   : 12px;
  font-weight    : 500;
  font-family    : "Montserrat", sans-serif;
  font-size      : 14px;
  color          : var(--dark);
  text-decoration: none;
}

.flight__airport-city {
  margin    : 0;
  margin-top: 9px;
  color     : var(--dark);
}

.route__duration {
  margin-top   : 38px;
  margin-bottom: 35px;
}

.route__duration {
  display    : flex;
  align-items: center;
}

.route__duration-text {
  margin: 0;
}

.route__duration-img {
  margin-right : 10px;
  margin-left  : 8px;
  margin-bottom: 2px;
}

.route__actions {
  display       : flex;
  flex-direction: column;
  margin-top    : 12px;
}

.route__link {
  display        : flex;
  align-items    : center;
  margin-bottom  : 16px;
  color          : var(--main-grey);
  text-decoration: underline;

  &:last-child {
    margin-bottom: 0;
  }
}

.route__actions-img {
  stroke: var(--main-grey);
  fill  : none;

  &+.route__link-text {
    margin-left: 12px;
  }
}

.route__link-text+.route__actions-img {
  margin-left: 14px;
}

.route__flight-time-diff {
  position : relative;
  top      : -10px;
  left     : -5px;
  font-size: 10px;
  color    : var(--main-grey);
}


@media (max-width: 767px) {

  .route__head,
  .route__details {
    grid-template-columns: 24px 1fr;
  }

  .route__date {
    grid-column-start: 2;
    margin-top       : 13px;
  }

  .route__actions {
    grid-column-start: 2;
    margin-top       : 30px;
  }
}

@media (max-width: 500px) {

  .route__head,
  .route__details {
    column-gap: var(--main-padding);
  }

  .flight__airport-abbr {
    display      : block;
    margin-bottom: 9px;
  }

  .route__flight-info {
    grid-template-columns: 55px 1fr;
  }

  .route__icon-place:last-child {
    margin-bottom: 37px;
  }
}
