:root {
  --white: #FFFFFF;
  --balck: #000000;
  --dark: #292949;
  --darken-grey: #F5F8FE;
  --main-grey: #7B819A;
  --light-grey: #E5E5E5;
  --orange: #FAB43F;
  --pink: #F96878;
  --blue: #A0C4F7;
  --purple: #6A4EFF;
  --yellow: #F2C94C;
  --booking-bg: #61DBB4;
  --main-shadow: 0px 2px 7px rgba(0, 0, 0, 0.04);
  --main-border-radius: 5px;
  --main-border: 1px solid #E9EDF3;
  --main-row-gap: 31px;
  --main-padding: 35px;
  --member-color: #848AA0;
  --confirmed-color: #4DC7A0;
  --confirmed-bg: rgba(97, 219, 180, 0.2);
  --more-info-color: #B0B3C2;
  --fee-free: #57D1AA;
  --route-date-bg: #F3F5F8;
  --duration-color: #C6CAD9;
  --whatsapp: #2CB742;
  --telegramm: #41B4E6;
  --disable: #DADADA;
}
@media (max-width: 500px) {
  :root {
    --main-padding: 10px;
  }
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 130%;
  letter-spacing: 0.03em;
  font-family: "Roboto", sans-serif;
  color: var(--main-grey);
  background: var(--light-grey);
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
*,
::before,
::after {
  box-sizing: border-box;
}
.page-header {
  min-height: 60px;
  margin-bottom: 50px;
  background-color: var(--white);
  box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.06);
}
.container {
  width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}
@media (max-width: 1200px) {
  .container {
    width: 768px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 100%;
  }
}
@media (max-width: 500px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}
.columns {
  display: grid;
  grid-template-columns: 420px 1fr;
  grid-column-gap: 30px;
}
@media (max-width: 1200px) {
  .columns {
    grid-template-columns: 1fr;
    row-gap: var(--main-row-gap);
  }
}
.info {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: min-content;
  row-gap: var(--main-row-gap);
}
.info__item {
  background-color: var(--white);
  border-radius: var(--main-border-radius);
  box-shadow: var(--main-shadow);
}
@media (max-width: 1200px) {
  .info {
    order: 1;
  }
}
.heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: 76px;
  padding-left: var(--main-padding);
  padding-right: 30px;
  padding-top: 26px;
  padding-bottom: 26px;
  border-bottom: var(--main-border);
}
.heading__title {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 163%;
  color: var(--dark);
}
@media (max-width: 500px) {
  .heading {
    padding-right: var(--main-padding);
  }
}
.info-booking {
  margin: 0;
  padding: 2px 10px;
  font-size: 12px;
  line-height: 163%;
  background-color: var(--booking-bg);
  border-radius: var(--main-border-radius);
  color: var(--white);
}
.data {
  padding-left: var(--main-padding);
  padding-right: var(--main-padding);
  padding-top: 34px;
  padding-bottom: 40px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
}
.data--price {
  padding-bottom: 35px;
  padding-top: 30px;
}
.data__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 30px;
  align-items: center;
  margin: 0;
}
.data__name--tax {
  display: flex;
  align-items: center;
}
.data__value {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0;
  text-align: right;
}
.data__value-confirmed {
  display: flex;
  width: fit-content;
  align-items: center;
  margin: 0;
  padding-top: 3px;
  padding-right: 12px;
  padding-bottom: 3px;
  padding-left: 9px;
  font-weight: 600;
  background-color: var(--confirmed-bg);
  border-radius: calc(var(--main-border-radius) * 4);
  color: var(--confirmed-color);
}
.data__value-confirmed-img {
  margin-right: 5px;
}
.data__name-member {
  display: inline-block;
  margin: 0;
  padding-top: 3px;
  padding-bottom: 2px;
  padding-left: 12px;
  padding-right: 12px;
  background-color: var(--darken-grey);
  border-radius: 4px;
  color: var(--member-color);
}
.data__value-fee {
  margin: 0;
}
.data__value-fee--free {
  color: var(--fee-free);
}
.avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 12px;
  width: 20px;
  height: 20px;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 9px;
  line-height: 163%;
  border-radius: 30px;
  color: var(--white);
}
.avatar--big {
  width: 27px;
  height: 27px;
  margin: 0;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15);
  font-size: 13px;
  border-radius: 30px;
}
.avatar--orange {
  background-color: var(--orange);
}
.avatar--pink {
  background-color: var(--pink);
}
.avatar--blue {
  background-color: var(--blue);
}
.avatar--purple {
  background-color: var(--purple);
}
.avatar--yellow {
  background-color: var(--yellow);
}
.passenger {
  margin: 0;
  padding: 0;
  list-style: none;
}
.passenger__item:not(:last-child) {
  margin-bottom: 35px;
}
.passenger__person {
  margin-bottom: 24px;
}
.person {
  display: flex;
  align-items: center;
  text-align: right;
}
.luggage {
  margin: 0;
  padding: 0;
  list-style: none;
}
.luggage__item {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 20px;
  padding: 5px 32px;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  line-height: 163%;
  background: var(--darken-grey);
  border-radius: 4px;
}
.luggage__item:last-child {
  margin-bottom: 0;
}
.luggage__item p {
  margin: 0;
}
.travel {
  display: flex;
  margin-bottom: 35px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 13px;
  color: var(--dark);
}
.travel__type,
.travel__count {
  margin: 0;
}
.travel__divider {
  margin-right: 16px;
  margin-left: 16px;
}
.more-info {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 12px;
  height: 12px;
  margin-left: 8px;
  font-size: 8px;
  border: 1px solid var(--more-info-color);
  border-radius: 50%;
  cursor: pointer;
}
.more-info__img {
  fill: currentColor;
}
.total {
  padding-top: 26px;
  font-weight: 500;
  font-size: 14px;
  line-height: 163%;
  border-top: var(--main-border);
  color: var(--dark);
}
.price {
  padding-bottom: 34px;
}
.page-main {
  padding-bottom: 222px;
}
@media (max-width: 768px) {
  .page-main {
    padding-bottom: 50px;
  }
}
.route__item {
  padding: var(--main-padding);
  padding-bottom: 42px;
  border-bottom: 1px dashed rgba(212, 219, 231, 0.7);
}
.route__item:last-child {
  border-bottom: none;
}
.route__head {
  display: grid;
  grid-template-columns: 24px 1fr max-content;
  column-gap: 30px;
  margin-bottom: var(--main-row-gap);
}
.route__date {
  margin-top: 6px;
}
.route__company {
  margin-top: 7px;
  margin-bottom: 13px;
}
.route__divider {
  margin-left: 4px;
  margin-right: 2px;
}
.route__divider::before {
  content: "\0000b7";
}
.route__date-value {
  padding: 3px 10px;
  opacity: 0.8;
  border-radius: 5px;
  background-color: var(--route-date-bg);
}
.route__duration-img {
  fill: none;
  stroke: var(--main-grey);
}
.route__details {
  display: grid;
  grid-template-columns: 24px minmax(0, 416px) 1fr;
  column-gap: 30px;
}
.route__icon-place {
  width: 9px;
  height: 9px;
  border: 1px solid var(--duration-color);
  border-radius: 12px;
}
.route__icon-place:first-child {
  margin-top: 15px;
}
.route__icon-place:last-child {
  margin-bottom: 17px;
}
.route__icon-duration {
  flex: 1;
  width: 1px;
  margin-top: 2px;
  margin-bottom: 2px;
  border-right: 1px solid var(--duration-color);
}
.route__incons {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.route__flight-info {
  display: grid;
  grid-template-columns: 67px 1fr;
}
.route__flight-time {
  padding-top: 13px;
  font-weight: 500;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  color: var(--dark);
}
.flight__airport-text {
  margin: 0;
}
.flight__airport-abbr {
  margin-right: 12px;
  font-weight: 500;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  color: var(--dark);
  text-decoration: none;
}
.flight__airport-city {
  margin: 0;
  margin-top: 9px;
  color: var(--dark);
}
.route__duration {
  margin-top: 38px;
  margin-bottom: 35px;
}
.route__duration {
  display: flex;
  align-items: center;
}
.route__duration-text {
  margin: 0;
}
.route__duration-img {
  margin-right: 10px;
  margin-left: 8px;
  margin-bottom: 2px;
}
.route__actions {
  display: flex;
  flex-direction: column;
  margin-top: 12px;
}
.route__link {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  color: var(--main-grey);
  text-decoration: underline;
}
.route__link:last-child {
  margin-bottom: 0;
}
.route__actions-img {
  stroke: var(--main-grey);
  fill: none;
}
.route__actions-img + .route__link-text {
  margin-left: 12px;
}
.route__link-text + .route__actions-img {
  margin-left: 14px;
}
.route__flight-time-diff {
  position: relative;
  top: -10px;
  left: -5px;
  font-size: 10px;
  color: var(--main-grey);
}
@media (max-width: 767px) {
  .route__head,
  .route__details {
    grid-template-columns: 24px 1fr;
  }
  .route__date {
    grid-column-start: 2;
    margin-top: 13px;
  }
  .route__actions {
    grid-column-start: 2;
    margin-top: 30px;
  }
}
@media (max-width: 500px) {
  .route__head,
  .route__details {
    column-gap: var(--main-padding);
  }
  .flight__airport-abbr {
    display: block;
    margin-bottom: 9px;
  }
  .route__flight-info {
    grid-template-columns: 55px 1fr;
  }
  .route__icon-place:last-child {
    margin-bottom: 37px;
  }
}
.guest__item {
  display: grid;
  row-gap: 22px;
  padding-top: 26px;
  padding-left: var(--main-padding);
  padding-right: var(--main-padding);
}
.guest__row {
  display: grid;
  grid-template-columns: 27px repeat(3, 1fr);
  column-gap: 17px;
  align-items: center;
}
.guest__row:last-child {
  padding-bottom: 38px;
  border-bottom: var(--main-border);
}
.guest__item-col {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.guest__item-col--second {
  grid-column-start: 2;
}
.guest__item-col--full {
  grid-column-end: -1;
}
.guest__item-value {
  margin: 0;
  font-size: 12px;
  line-height: 185%;
  letter-spacing: 0;
}
.guest__item-value--head {
  font-size: 13px;
  letter-spacing: 0.03em;
}
.guest__photo {
  margin-left: 8px;
}
.guest__social {
  margin-left: 20px;
}
.guest__social--whatsapp {
  fill: var(--whatsapp);
}
.guest__social-link:not(:last-child) {
  margin-right: 10px;
}
.guest__img-social--whatsapp {
  fill: var(--whatsapp);
}
.guest__img-social--telegramm {
  fill: var(--telegramm);
}
.guest__img-social--disable {
  fill: var(--disable);
}
.guest__item-name {
  margin: 0;
  margin-right: 6px;
  font-size: 11px;
  color: var(--dark);
  letter-spacing: 0;
}
.guest__flyer-divider {
  margin-left: 10px;
  margin-right: 10px;
}
@media (max-width: 767px) {
  .guest__row {
    grid-template-columns: 27px 1fr;
    row-gap: 10px;
  }
  .guest__item-col {
    grid-column-start: 2;
  }
  .guest__item-col--avatar {
    grid-column-start: 1;
  }
}
.main {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: min-content;
  row-gap: var(--main-row-gap);
}
.loyality {
  padding-top: 37px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 50px;
}
.loyality__row {
  display: flex;
  align-items: center;
  padding-bottom: 15px;
  border-bottom: var(--main-border);
}
.loyality__name {
  margin: 0;
  margin-right: 21px;
  font-size: 15px;
  line-height: 163%;
}
.loyality__value {
  margin: 0;
  font-size: 12px;
  line-height: 163%;
}
@media (max-width: 500px) {
  .loyality {
    padding-left: var(--main-padding);
    padding-right: var(--main-padding);
  }
}
