.container {
  width        : 1200px;
  margin       : 0 auto;
  padding-left : 20px;
  padding-right: 20px;
}

@media (max-width: 1200px) {
  .container {
    width: 768px;
  }
}

@media (max-width: 767px) {
  .container {
    width: 100%;
  }
}

@media (max-width: 500px) {
  .container {
    padding-left : 10px;
    padding-right: 10px;
  }
}
