.data {
  padding-left  : var(--main-padding);
  padding-right : var(--main-padding);
  padding-top   : 34px;
  padding-bottom: 40px;
  font-family   : "Montserrat", sans-serif;
  font-weight   : 500;

  &--price {
    padding-bottom: 35px;
    padding-top   : 30px;
  }
}

.data__list {
  display              : grid;
  grid-template-columns: 1fr 1fr;
  row-gap              : 30px;
  align-items          : center;
  margin               : 0;
}

.data__name {
  &--tax {
    display    : flex;
    align-items: center;
  }
}

.data__value {
  display        : flex;
  justify-content: flex-end;
  align-items    : center;
  margin         : 0;
  text-align     : right;
}

.data__value-confirmed {
  display         : flex;
  width           : fit-content;
  align-items     : center;
  margin          : 0;
  padding-top     : 3px;
  padding-right   : 12px;
  padding-bottom  : 3px;
  padding-left    : 9px;
  font-weight     : 600;
  background-color: var(--confirmed-bg);
  border-radius   : calc(var(--main-border-radius) * 4);
  color           : var(--confirmed-color);
}

.data__value-confirmed-img {
  margin-right: 5px;
}

.data__name-member {
  display         : inline-block;
  margin          : 0;
  padding-top     : 3px;
  padding-bottom  : 2px;
  padding-left    : 12px;
  padding-right   : 12px;
  background-color: var(--darken-grey);
  border-radius   : 4px;
  color           : var(--member-color);
}

.data__value-fee {
  margin: 0;

  &--free {
    color: var(--fee-free);
  }
}
