.luggage {
  margin    : 0;
  padding   : 0;
  list-style: none;
}

.luggage__item {
  display        : flex;
  justify-content: space-between;
  flex-wrap      : wrap;
  margin-bottom  : 20px;
  padding        : 5px 32px;
  font-family    : "Roboto", sans-serif;
  font-size      : 12px;
  line-height    : 163%;
  background     : var(--darken-grey);
  border-radius  : 4px;
}

.luggage__item:last-child {
  margin-bottom: 0;
}

.luggage__item p {
  margin: 0;
}
