.avatar {
  display        : flex;
  justify-content: center;
  align-items    : center;
  margin-right   : 12px;
  width          : 20px;
  height         : 20px;
  font-family    : "Roboto", sans-serif;
  font-weight    : 500;
  font-size      : 9px;
  line-height    : 163%;
  border-radius  : 30px;
  color          : var(--white);

  &--big {
    width        : 27px;
    height       : 27px;
    margin       : 0;
    box-shadow   : 0px 1px 2px rgba(0, 0, 0, 0.15);
    font-size    : 13px;
    border-radius: 30px;
  }

  &--orange {
    background-color: var(--orange);
  }

  &--pink {
    background-color: var(--pink);
  }

  &--blue {
    background-color: var(--blue);
  }

  &--purple {
    background-color: var(--purple);
  }

  &--yellow {
    background-color: var(--yellow);
  }
}
