.loyality {
  padding-top   : 37px;
  padding-left  : 40px;
  padding-right : 40px;
  padding-bottom: 50px;
}

.loyality__row {
  display       : flex;
  align-items   : center;
  padding-bottom: 15px;
  border-bottom : var(--main-border);
}

.loyality__name {
  margin      : 0;
  margin-right: 21px;
  font-size   : 15px;
  line-height : 163%;
}

.loyality__value {
  margin     : 0;
  font-size  : 12px;
  line-height: 163%;
}

@media (max-width: 500px) {
  .loyality {
    padding-left : var(--main-padding);
    padding-right: var(--main-padding);
  }
}
