:root {
  --white: #FFFFFF;
  --balck: #000000;

  --dark: #292949;

  --darken-grey: #F5F8FE;
  --main-grey  : #7B819A;
  --light-grey : #E5E5E5;

  --orange: #FAB43F;
  --pink  : #F96878;
  --blue  : #A0C4F7;
  --purple: #6A4EFF;
  --yellow: #F2C94C;

  --booking-bg: #61DBB4;

  --main-shadow       : 0px 2px 7px rgba(0, 0, 0, 0.04);
  --main-border-radius: 5px;
  --main-border       : 1px solid #E9EDF3;
  --main-row-gap      : 31px;
  --main-padding      : 35px;
  --member-color      : #848AA0;
  --confirmed-color   : #4DC7A0;
  --confirmed-bg      : rgba(97, 219, 180, 0.2);
  --more-info-color   : #B0B3C2;
  --fee-free          : #57D1AA;
  --route-date-bg     : #F3F5F8;
  --duration-color    : #C6CAD9;
  --whatsapp          : #2CB742;
  --telegramm         : #41B4E6;
  --disable           : #DADADA;
}


@media (max-width: 500px) {
  :root {
    --main-padding: 10px;
  }
}
